
body {
    font-family: Arial, sans-serif;
    background-color: #f4f6f8;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 600px;
    margin: 40px auto;
    padding: 10px;
  }
  
  .card {
    background-color: white;
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 12px;
    margin-bottom: 20px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.05);
  }
  
  .card h2 {
    font-size: 1.25rem;
    margin-bottom: 15px;
  }
  
  input,
  select {
    display: block;
    width: 100%;
    margin-bottom: 12px;
    padding: 8px;
    border-radius: 6px;
    border: 1px solid #ccc;
  }
  
  button {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    margin-top: 5px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .btn-group {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  .logout-btn {
    background-color: #dc3545;
    margin-bottom: 20px;
  }
  
  .logout-btn:hover {
    background-color: #a71d2a;
  }
  

  .fade-slide {
    opacity: 0;
    transform: translateY(15px);
    animation: fadeSlideIn 0.3s forwards;
    animation-delay: 0.1s;
    }
    
    @keyframes fadeSlideIn {
    to {
    opacity: 1;
    transform: translateY(0);
    }
    }
    
    /* Hover for buttons */
    button {
    transition: background-color 0.3s ease, color 0.3s ease;
    cursor: pointer;
    }
    
    button:hover {
    background-color: #2563eb;
    color: white;
    }
    
    /* Input focus */
    input[type='text'],
    input[type='password'],
    select {
    border: 1px solid #9ca3af;
    padding: 0.4rem;
    border-radius: 4px;
    transition: border-color 0.3s ease;
    }
    
    input[type='text']:focus,
    input[type='password']:focus,
    select:focus {
    border-color: #2563eb;
    outline: none;
    }
    
    /* Dark mode adjustments */
    .app-container.dark {
    background-color: #1e293b;
    color: #e0e7ff;
    }
    
    .app-container.dark .card {
    background-color: #334155;
    border-color: #475569;
    }
    
    .app-container.dark input,
    .app-container.dark select {
    background-color: #475569;
    color: #e0e7ff;
    border-color: #64748b;
    }
    
    .app-container.dark button:hover {
    background-color: #3b82f6;
    }
    
    /* Card styles */
    .card {
    background: white;
    border-radius: 10px;
    padding: 1rem;
    box-shadow: 0 4px 8px rgb(0 0 0 / 0.1);
    margin-bottom: 1rem;
    }


    /* Import Courier Sans - fallback to monospace */
@import url('https://fonts.cdnfonts.com/css/courier-sans');

/* Apply Courier Sans globally */
body, .app-container, .card, .password-card, input, select, button {
  font-family: 'Courier Sans', monospace, Courier, monospace;
}

/* Dark mode adjustments */
.app-container.dark {
  background-color: #1e293b;
  color: #e0e7ff;
}

.app-container.dark .card,
.app-container.dark .password-card {
  background-color: #334155;
  border-color: #475569;
  color: #e0e7ff;
}

.app-container.dark input,
.app-container.dark select {
  background-color: #475569;
  color: #e0e7ff;
  border-color: #64748b;
}

.app-container.dark button:hover {
  background-color: #3b82f6;
}

/* Password card specific */
.password-card {
  background: white;
  border-radius: 10px;
  padding: 1rem;
  box-shadow: 0 4px 8px rgb(0 0 0 / 0.1);
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.login-card {
    width: 320px;
    margin: 10vh auto; /* vertically center with margin */
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    background-color: #f3f4f6; /* light bg for light mode */
    font-family: 'Courier New', Courier, monospace;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .login-card.dark {
    background-color: #1f2937; /* dark bg */
    color: #e5e7eb;
    box-shadow: 0 4px 15px rgba(255, 255, 255, 0.1);
  }
  
  .login-title {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #0ea5e9;
    font-weight: bold;
    text-align: center;
  }
  